html {
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100%;
}

body {
  position: absolute;
  width: 80%;
  left: 10%;
  top: 5%;
}

/* Diagram Additions Start */

.diagram-container{
  display: block;
  position: absolute;
  width: 1500px;
  height: 1000px;
  border: black;
  margin-bottom: 20px;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
}

.diagram-full{
  display: inline-block;
  position: absolute;
  background-color: white;
  border-color: black;
  border-width: 2px;
  border-style: solid;
  margin: 0;
}

.diagram-full-container{
  display: inline-block;
  background-color: #999999;
  position: absolute;
  left: 250px;
  width: 1000px;
  height: 1000px;
  border-color: black;
  border-width: 2px;
  border-style: solid;
  overflow: auto;
}

.panel-left{
  display: inline-block;
  position: absolute;
  width: 250px;
  height: 1000px;
  border-color: black;
  border-width: 2px;
  border-style: solid;
  background-color: #F7F6F6;
}

.panel-right{
  display: inline-block;
  position: absolute;
  width: 250px;
  height: 1000px;
  left: 1250px;
  border-color: black;
  border-width: 2px;
  border-style: solid;
  background-color: #F7F6F6;
  padding: 10px;
}

.diagram-options{
  display: block;
}

.container-option{
  text-align: center;
  margin-top: 30px;
  margin-bottom: 10px;
  font-weight: bold;
}

.input-example{
  font-weight:normal;
  margin-top: 20px;
}

.option{
  font-weight: bold;
  font-size: 12pt;
  color: black;
}

.diagram-box{
  height: inherit;
  width: inherit;
}

.diagram-text{
  margin: 0;
  top: 50%;
  left: 50%;
  position: absolute;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.diagram-label{
  margin: 0;
  top: 50%;
  text-align:center;
  position: absolute;
  -ms-transform: translate(0%, -50%);
  transform: translate(0%, -50%);
}

.properties{
  display: none;
}

.resize-handle{
  z-index: 10;
  background-color: white;
  border-style: solid;
  border-width: 1px;
  display: none;
}

.add-box{
  text-decoration: none;
  height:auto;
  width: 100%;
  color: black;
  cursor: pointer;
}

.container-sample{
  position: relative;
  width:60px;
  height:60px;
  background-color: white;
  border-style: solid;
  left:50%;
  -ms-transform: translate(-50%);
  transform: translate(-50%);
}

.button-sample{
  z-index: 0;
  position: relative;
  pointer-events: none;
  margin-top: 20px;
  width:100px;
  left:50%;
  -ms-transform: translate(-50%);
  transform: translate(-50%);
}

.normal{
  z-index: 0;
  position: relative;
  pointer-events: none;
  width:100px;
  left:50%;
  -ms-transform: translate(-50%);
  transform: translate(-50%);
}

.button-text{
  height: 100%;
  width: 100%;
  border-style: solid;
}

.textfield-text{
  color:#BEBEBE;
}

.zoomPic{
  margin: 5px;
  height: 25px;
  width: auto;
}

.zoom-header{
  border-bottom: solid;
  padding: 5px;
}

.zoomOption{
  position: relative;
  margin-left: 20px;
  top: -10px;
}

.card .deleteListButton{
  font-weight: bold;
}

.pointer-div{
  cursor: pointer;
}

/* Diagram Additions End */

.fixed-action-btn{
  position: absolute;
}

.item-form-text{
  font-size:28px;
  font-weight: bold;
}

.container{
  width: 85%;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 10px;
  margin-top: 10px;
}

.add-card{
  font-size: 40px;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
}

.trashcan{
  font-size: 40px;
}

.trash-col{
  text-align:right;
}

.item-check{
  margin-right: 20px;
}

.btn-padding{
  margin: 5px;
  margin-bottom: 20px;
}

.action-button{
  margin-top:4px;
}

.active .action-index{
  margin-right: 0px;
  opacity: 1;
  transition: margin-right .4s, opacity .4s ease-in;
}

.action-index{
  margin-right:-80px;
  opacity: 0;
}

.nav-wrapper{
  padding-bottom: 20px;
  margin-bottom: 20px;
}

.category{
  cursor: pointer;
  font-weight: bold;
  font-size: 28pt;
}

.home_new_list_container{
  text-align:center;
  padding: 20px;
}

.action-index{
  z-index: 9999;
}

.modal{
  width: max-content;
}

div.banner {
  padding: 20px;
  margin-top: 20px;
}

form {
  padding: 20px;
  margin-top: 60px;
}

form button, form h5 {
  margin: 20px 0;
}

input[type=text]:not(.browser-default):focus:not([readonly]),
input[type=email]:not(.browser-default):focus:not([readonly]),
input[type=password]:not(.browser-default):focus:not([readonly]),
textarea.materialize-textarea:focus:not([readonly]){
  border-color: #ec407a;
  box-shadow: none;
}

input[type=text]:not(.browser-default):focus:not([readonly]) + label,
textarea.materialize-textarea:focus:not([readonly]) + label,
input[type=email]:not(.browser-default):focus:not([readonly]) + label,
input[type=password]:not(.browser-default):focus:not([readonly]) + label{
  color: #ec407a !important;
}
